@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,300;1,400&display=swap);
* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	font-family: "Lato", sans-serif;
	color: white;
}

body {
	background-color: rgb(4, 14, 31);
}

.title {
	display: flex;
	justify-content: center;
	margin: 30px;
	margin-bottom: 60px;
	font-size: 55px;
	font-weight: 600;
	color: white;
}

.controls-wrapper {
	display: flex;
	justify-content: space-around;
}

.button-wrapper {
	display: flex;
	justify-content: space-between;
}

.mode-btn {
	background-color: rgba(255, 255, 255, 0);
	padding: 10px 20px;
	border: 1px solid #00b4d8;
	border-radius: 10px;
	margin: 0 20px;
	transition: all 300ms ease;
	outline: none;
	color: rgba(255, 255, 255, 0.8);
	font-size: 18px;
}

.selected-btn {
	background-color: #00b4d81d;
	padding: 10px 20px;
	border: 1px solid #00b4d8;
	border-radius: 10px;
	margin: 0 20px;
	transition: all 300ms ease;
	outline: none;
	color: rgba(255, 255, 255, 0.8);
	font-size: 18px;
}

.mode-btn:hover,
.selected-btn:hover {
	transition: all 300ms ease;
	outline: none;
	cursor: pointer;
}

.location {
	display: flex;
	justify-content: center;
	font-size: 45px;
	margin-top: 30px;
	margin-bottom: 10px;
	color: rgba(255, 255, 255, 0.9);
}

.information {
	color: rgba(255, 255, 255, 0.8);
	display: flex;
	align-items: center;
	flex-direction: column;
	margin-bottom: 80px;
	word-spacing: 3px;
	font-size: 20px;
	font-weight: 300;
}

.code-link {
	color: #00b4d8;
}

.smaller-text {
	font-size: 16px;
	padding-top: 5px;
	color: rgba(255, 255, 255, 0.6);
}

.card-wrapper {
	background-color: rgba(255, 255, 255, 0.1);
	display: flex;
	align-items: center;
	flex-direction: column;
	width: 30%;
	border-radius: 10px;
	padding: 20px;
}

.card-stat {
	font-weight: 600;
	font-size: 40px;
	color: rgba(255, 255, 255, 0.8);
}

.card-title {
	font-weight: 400;
	color: rgba(255, 255, 255, 0.7);
	font-weight: 300;
	font-size: 25px;
}

.cards-wrapper {
	display: flex;
	justify-content: space-around;
	margin: 0 5%;
}

.latest-date {
	font-style: italic;
	font-weight: 300;
	display: flex;
	justify-content: center;
	margin-bottom: 5px;
	color: rgba(255, 255, 255, 0.7);
	word-spacing: 3px;
	font-size: 18px;
}

.small-chart-description {
	margin-left: auto;
	font-style: italic;
	font-size: 14px;
	font-weight: 200;
	margin-top: 3px;
	color: rgba(255, 255, 255, 0.6);
}

.small-chart-wrapper {
	width: 100%;
}

.chart-wrapper {
	margin: 0 auto;
	width: 80%;
	margin-top: 40px;
	margin-bottom: 60px;
	background-color: rgba(255, 255, 255, 0.1);
	padding: 30px;
	border-radius: 10px;
	color: white;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.chart-title {
	font-size: 25px;
	word-spacing: 4px;
	font-weight: 400;
	color: rgba(255, 255, 255, 0.9);
	margin-bottom: 20px;
}

.chart {
	width: 100%;
}

.chart-wrapper p {
	color: rgba(255, 255, 255, 0.6);
	font-style: italic;
	font-weight: 100;
	font-size: 14px;
	word-spacing: 2px;
}

.dropdown {
	padding: 10px !important;
	border-radius: 10px;
	border: 1px solid #00b4d8 !important;
	transform: scale(1.1);
}

.dropdown-select {
	color: rgba(255, 255, 255, 0.8) !important;
	border-bottom: 1px solid #00b4d8 !important;
}

.dropdown-select::before {
	color: rgba(255, 255, 255, 0.8) !important;
	border-bottom: 0px solid #00b4d8 !important;
}

.dropdown-select::after {
	color: rgba(255, 255, 255, 0.8) !important;
	border-bottom: 0px solid #00b4d8 !important;
}

.arrow {
	display: none !important;
}

